body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  padding: 0;
}

.PlayButton {
  color: #999;
  font-size: 25px;
  width: 40px;
  height: 40px;
  text-decoration: none;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
}

.PlayButton:active {
  background: #eee;
}

.PlayButton:active {
  background-color: #eee;
}

.PlayButtonWithText {
  width: auto;
  padding: 7px 13px;
}

.PlayIcon {
  display: inline-block;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 15px solid #999;
  box-sizing: border-box;
  width: 10px;
  height: 20px;
  margin-left: 2px;
  vertical-align: bottom;
}

.PlayAllButton {
  color: #999;
  font-size: 15px;
  padding: 5px 20px;
  border: 1px solid #ccc;
  text-decoration: none;
  box-sizing: border-box;
  display: inline-block;
  text-align: center;
}
.PlayAllButton:active {
  background: #eee;
}

span {
  pointer-events: none;
}

.App {
  padding: 0 10px;
}

.anchers {
  display: flex;
  justify-content: flex-end;
}

.fav-teams-container {
  max-width: 400px;
  margin: 0 auto;
}

.game-list {
  max-width: 400px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding-top: 10px;
}

.play-all {
  margin-bottom: 5px;
  text-align: right;
}

header {
  display: flex;
  align-items: baseline;
  border-bottom: 1px solid #ccc;
  padding-bottom: 0;
}

h1 {
  flex: 1 1;
  text-align: left;
}

h1 {
  margin: 10px 0;
  font-size: 20px;
}

.LastUpdate {
  font-size: 14px;
}

footer {
  max-width: 400px;
  margin: 0 auto;
}

.GoToTop {
  display: block;
  padding: 10px;
  text-align: center;
  background: #eee;
  color: #555;
  text-decoration: none;
}

.Ranking {
  overflow: hidden;
  margin-bottom: 30px;
  padding-top: 10px;
  max-width: 400px;
  margin: 0 auto;
}

.Ranking .Ranking-body ul {
  float: left;
  width: 50%;
}

.Ranking .Ranking-body ul li .rank {
  margin-right: 10px;
  width: 20px;
  display: inline-block;
}

.Ranking .Ranking-body ul li img {
  width: 30px;
  vertical-align: middle;
  margin-right: 5px;
}

.Ranking .Ranking-body ul li  {
  padding-bottom: 5px;
  margin-bottom: 5px;
  border-bottom: 1px solid #eee;
  font-size: 12px;
}

.Ranking .Ranking-body ul li.rank-7  {
  border-bottom: 1px solid #999;
}

.Ranking-body .team-name {
  width: 26px;
  display: inline-block;
}

.Ranking-body .win-record {
  color: #999;
  margin-left: 3px;
  font-size: 11px;
  display: inline-block;
  width: 38px;
}

.Ranking-body .behind {
  margin-left: 3px;
  font-size: 11px;
  color: #ED074F;
}

.Ranking h3 {
  font-size: 15px;
  margin: 10px 0;
}

.Ranking-header {
  overflow: hidden;
  margin-bottom: 10px;
}
.video-play-section {
  position: relative;
}

.videoContainer video {
  width: 100%;
}

.playing-progress {
  position: absolute;
  color: white;
  top: 5px;
  opacity: 0.5;
  text-align: center;
  width: 100%;
}

.player-buttons {
  position: absolute;
  top: 5px;
  text-align: right;
  width: 100%;
}

.player-buttons button {
  margin: 5px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: 1px solid white;
  background: none;
  color: white;
  padding: 10px 20px;
  opacity: 0.5;
  border-radius: 5px;
}

.player-buttons button[disabled] {
  margin: 5px;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: 1px solid white;
  background: none;
  color: white;
  padding: 10px 20px;
  opacity: 0;
}

.PageAnchers {
  display: flex;
  margin-top: 2px;
}


.PageAnchers li a, .PageAnchers li span {
  color: #4078c0;
  font-size: 13px;
  padding: 10px 5px;
  display: inline-block;
}

.PageAnchers li span {
  color: #555;
  font-weight: bold;
}

.Ranking-header h3 {
  float: left;
}

.Ranking-header .PageAnchers {
  float: right;
}
.TeamIcon {
  display: inline-block;
  text-align: left;
  position: relative;
  width: 50px;
}

.TeamIcon > span {
  display: block;
  font-size: 12px;
  color: #555;
}

.TeamIcon img {
  width: 35px;
}
.TeamIcon .conf-w,
.TeamIcon .conf-e {
  display: inline-block;
  position: absolute;
  bottom: 0;
  right: 0;
  font-size: 10px;
  color: white;
  width: 15px;
  height: 13px;
  text-align: center;
  border-radius: 3px;
  opacity: 0.7;
  line-height: 14px;
}

.TeamIcon .conf-w {
  background-color: #006bb6;
}

.TeamIcon .conf-e {
  background-color: #ed074f;
}

.GameItem {
  margin-bottom: 15px;
}

.GameItem .line {
  display: flex;
  padding: 10px;
  margin: 0;
}

.GameItem .GameDate {
  padding: 5px;
  font-size: 13px;
  color: #555;
}

.GameItem.fav-level-1 .GameDate {
  position: relative;
  color: #4078c0;
}

.GameItem.fav-level-2 .GameDate {
  position: relative;
  color: #4078c0;
  font-weight: bold;
}

.GameSide {
  display: flex;
  align-items: center;
  flex: 1 1;
}

.GameSide .score {
  width: 40px;
  text-align: right;
  margin-left: 0;
  margin-right: 15px;
  font-size: 14px;
}

.GameSide .score.win {
  font-weight: bold;
  font-size: 16px;
}

.GameSide:first-of-type {
  margin-right: 10px;
}

.GameSide:first-of-type .score {
  text-align: left;
  margin-right: 0;
  margin-left: 15px;
}

.GameItem p {
  margin: 0;
  color: #555;
  font-size: 11px;
  padding: 0 5px;
  display: inline;
  font-weight: normal;
}

.GameRecap {
  display: flex;
  align-items: center;
  width: 50px;
  justify-content: flex-end;
}

.GameBadge {
  color: #55ae2e;
  padding: 3px;
  border-radius: 3px;
}

.GameItem .videoContainer video {
  width: 100%;
}

